<template>
    <el-card class="box-card">
        <div class="list_head" slot="header">
            <span>轮播图列表</span>
            <el-button v-permission="'promotion:slide:create'" @click="insertDialogVisible = true" type="primary"
                icon="el-icon-circle-plus-outline" plain size="mini">添加轮播图</el-button>
        </div>
        <el-table :data="tableData">
            <el-table-column prop="slide_id" label="#" width="100">
            </el-table-column>
            <el-table-column prop="title" label="链接标题">
            </el-table-column>
            <el-table-column label="轮播图片">
                <template slot-scope="scope">
                    <el-image :src="scope.row.picture"></el-image>
                </template>
            </el-table-column>
            <el-table-column label="跳转地址">
                <template slot-scope="scope">
                    <el-link :href="scope.row.url" :target="scope.row.target" :underline="false">
                        {{ scope.row.url }}</el-link>
                </template>
            </el-table-column>
            <el-table-column label="跳转方式">
                <template slot-scope="scope">
                    <span>{{ scope.row.target === '_self' ? '本页面打开' : '新页面打开' }}</span>
                </template>
            </el-table-column>
            <el-table-column label="排序数字">
                <template slot-scope="scope">
                    <el-tag type="success">{{ scope.row.slide_order }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="添加日期">
                <template slot-scope="scope">
                    {{ loacleTimestamp(scope.row.create_date) }}
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <!-- template插槽 内部放复杂结构 必须加slot-scope="scope"-->
                <template slot-scope="scope">
                    <el-button v-permission="'promotion:slide:edit'" @click="openEditDialog(scope.row)" icon="el-icon-edit"
                        size="mini" type="primary">编辑</el-button>
                    <el-button v-permission="'promotion:slide:remove'"
                        @click="handleRemove(scope.row.slide_id, scope.$index)" plain size="mini" icon="el-icon-delete"
                        type="danger">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 下面两个弹窗结构是相同的，可以封装成一个，但是耦合度高，不易于阅读 -->
        <!-- 添加 -->
        <el-dialog title="添加轮播图" :visible.sync="insertDialogVisible" @close="handleCloseDialog('insertForm')">
            <el-form :model="insertForm" :rules="rules" ref="insertForm" status-icon label-width="100px">
                <el-form-item label="轮播图片" prop="picture">
                    <ImgUpload v-model="insertForm.picture"></ImgUpload>
                </el-form-item>
                <el-form-item label="标题文字" prop="title">
                    <el-input type="text" v-model="insertForm.title" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="链接地址" prop="url">
                    <el-input type="text" v-model="insertForm.url" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="跳转方式" prop="target">
                    <el-radio-group v-model="insertForm.target">
                        <el-radio label="_blank">新页面打开</el-radio>
                        <el-radio label="_self">本页面打开</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="显示顺序" prop="slide_order">
                    <el-input type="number" v-model="insertForm.slide_order" autocomplete="off"
                        placeholder="输入数字，数字越小越靠前"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="insertDialogVisible = false">取 消</el-button>
                <!-- scope.row 为这一行的数据 -->
                <el-button @click="handleInsert" type="primary">添 加</el-button>
            </span>
        </el-dialog>

        <!-- 编辑 -->
        <el-dialog title="编辑轮播图" :visible.sync="editDialogVisible" @close="handleCloseDialog('editForm')">
            <el-form :model="editForm" :rules="rules" ref="editForm" status-icon label-width="100px">
                <el-form-item label="轮播图" prop="picture">
                    <ImgUpload v-model="editForm.picture"></ImgUpload>
                </el-form-item>
                <el-form-item label="标题文字" prop="title">
                    <el-input type="text" v-model="editForm.title" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="链接地址" prop="url">
                    <el-input type="text" v-model="editForm.url" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="显示顺序" prop="slide_order">
                    <el-input type="number" v-model="editForm.slide_order" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button @click="handleEdit" type="primary">保 存</el-button>
            </span>
        </el-dialog>
    </el-card>
</template>

<script>
import { Slide } from '@/api';

import ImgUpload from '@/components/Img-upload.vue';

export default {
    name: 'List',
    components: {
        ImgUpload,
    },
    data() {
        return {
            tableData: [],
            // 添加
            insertDialogVisible: false,
            insertForm: {
                slide_id: '',
                title: '',
                url: '',
                slide_order: '',
                picture: '',
                target: '',
                create_date: new Date().toLocaleString(),
            },
            // 编辑
            editForm: {
                title: '',
                url: '',
                slide_order: '',
                picture: '',
                target: '',
            },
            editDialogVisible: false,
            current_slide: '',
            // 表单验证规则
            rules: {
                picture: [
                    { required: true, message: '请上传图片！', trigger: 'blur' },
                ],
                title: [
                    { required: true, message: '请输入标题文字！', trigger: 'blur' },
                ],
                url: [
                    { required: true, message: '请输入网站地址！', trigger: 'blur' },
                    { pattern: /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/, message: '请正确输入网站地址！', trigger: 'blur' }
                ],
                slide_order: [
                    { required: true, message: '请输入排序位置(数字)！', trigger: 'blur' },
                ],
            },
        }
    },
    created() {
        this.loadList();
    },
    computed: {
        // 表达属性时，表达式很长，可用计算属性
        // 计算属性传参 ==> return一个函数
        loacleTimestamp() {
            return (timestamp) => {
                // "2023-02-27T02:17:49.000Z" 对于后台返回的这种时间，可让后台处理成正常时间，
                // 或者使用 new Date() 里面的 toLocaleString() 方法处理
                return new Date(timestamp).toLocaleString()
            }
        }
    },
    methods: {
        // 加载列表
        async loadList() {
            let { status, msg, data } = await Slide.list();
            if (status) {
                this.tableData = data;
            }
        },
        // 打开编辑弹窗
        openEditDialog(slide_data) {
            // 缓存 scope.row 的指针地址 ==> 所以 current_tag 改变的话 ，tag_data也会变。
            this.current_slide = slide_data;
            // 还原表单数据 ==> 把表单数据还原到 编辑弹窗 中。
            // 此处浅拷贝 ==> 断开与表格的数据的双向绑定 ==> 不然修改时未点击保存，表格数据也会随之变化。
            this.editForm = { ...slide_data };
            // 打开弹窗
            this.editDialogVisible = true;
        },
        // dialog组件的事件方法 ===> 所以点击事件写在组件上 ==> 去除上次表单验证的效果
        handleCloseDialog(formName) {
            this.$refs[formName].resetFields();
        },
        // 编辑
        async handleEdit() {
            try {
                let valid = await this.$refs.editForm.validate();
                if (valid) {
                    let { status, msg } = await Slide.edit({ ...this.editForm });
                    if (status) {
                        // 更新DOM
                        // current_slide ==> 联系 scope.row对象 的中间桥梁。 
                        Object.assign(this.current_slide, this.editForm);
                        // 关闭弹窗
                        this.editDialogVisible = false;
                        // 信息提示
                        this.$message.success(msg);
                    } else {
                        this.$message.error(msg);
                    }
                }
            } catch (error) {

            }
        },
        // 删除
        handleRemove(slide_id, i) {
            // 查阅文档 -- $confirm 返回一个 promise 对象 ==> 所以用 .then .catch 分别捕获。
            // 也可以 使用 async await ==> try catch 捕获
            this.$confirm('此操作将永久删除该角色, 是否继续?', '确认删除', {
                confirmButtonText: '删除',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // 请求数据 -- 必须填写 -- 可以多传但不能不传
                // 根据接口文档，此处需要 slide_id ,可以传整行数据，也可只传id ==> {slide_id:id} ==>注意：是对象
                let { status, msg } = await Slide.remove({ slide_id });
                if (status) {
                    // 后台数据已经删除 -- 但是页面并未更新 -- 需要操作DOM -- 删除
                    this.tableData.splice(i, 1);
                    // 信息提示
                    this.$message.success(msg);
                } else {
                    this.$message.error(msg);
                }
            }).catch(() => {
                this.$message.info('已取消删除');
            });
        },
        // 添加轮播图
        handleInsert() {
            // 通过$resfs 找到 insertForm
            this.$refs.insertForm.validate(async (valid) => {
                if (valid) {
                    let { status, msg, data } = await Slide.create({ ...this.insertForm });
                    if (status) {
                        // 添加 
                        // 因为 insertForm 为单层对象，所以浅拷贝即可，若是嵌套数组或者嵌套对象则需要深拷贝。
                        // 后台返回的 data数据 中 有 role_id 
                        this.tableData.push({ ...this.insertForm, ...data });
                        // 关闭弹窗
                        this.insertDialogVisible = false;
                        // 信息提示
                        this.$message.success(msg);
                    } else {
                        this.$message.error(msg);
                    }
                }
            })
        },
    }
}
</script>

<style lang="less" scoped>
.list_head {
    display: flex;
    justify-content: space-between;
}
</style>
